import React, { Component } from 'react'
import {Button,Modal,message} from 'antd'
import {withRouter} from 'react-router-dom'
import {postAction } from '@/axios';
class MiddleTop extends Component {
    handleReset=()=>{
        Modal.confirm({
            title:'清空组件',
            content:'确定清空全部组件吗？',
            okText:'确定',
            cancelText:'取消',
            onOk:()=>{
                this.props.handleReset()
            }
        })
    }
    handleSave=()=>{
        if(this.props.list.length>0){
            let tag=this.handleCheckValue()
            // 需要对于table类型的数据做处理
            if(tag){
                message.warning('唯一标识和控件名称不能为空')
            }else{
                let list=this.props.list
                let params={
                    "formId": this.props.param.id,
                    "columnProperties": JSON.stringify(list),
                }
                postAction('/ybt/form/save',params).then(res=>{
                    if(res.success){
                        message.success('操作成功') 
                        setTimeout(()=>{
                            window.close()
                        },1500)                        
                    }else{
                        message.error(res.obj)   
                    }
                })
            }            
        }else{
            message.warning('请先设计表单')
            return
        }
    }

    /**
     *
     *点击跳转预览页面，先调用保存的接口
     * @memberof MiddleTop
     */   
    handlePreview=()=>{
        if(this.props.list.length>0){
            let tag=this.handleCheckValue()
            // 需要对于table类型的数据做处理
            if(tag){
                message.warning('唯一标识和控件名称不能为空')
            }else{
                // 需要对于table类型的数据做处理
                let list=this.props.list
                let params={
                    "formId": this.props.param.id,
                    "columnProperties": JSON.stringify(list),
                }
                postAction('/ybt/form/save',params).then(res=>{
                    if(res.success){
                        window.open(`${window.location.origin}/#/formManagement/previewForm?id=${this.props.param.id}&type=preview`)
                    }else{
                        message.error(res.obj)   
                    }
                })
            }
            
        }else{
            message.warning('请先设计表单')
            return
        }
        // 新开一个页面预览
    }
    /**
     *检查是否有必填项没有填，如果有就不允许提交，主要针对唯一标识和控件名称     *
     * @memberof MiddleTop
     */
    handleCheckValue=()=>{
        let tag=false
        this.props.list.forEach(item=>{
            if(item.children && item.children.length>0){
                item.children.forEach(i=>{
                    if(i.common.onlyId === '' || i.common.name === ''){
                        tag=true
                    }
                })
            }
            if(item.common.onlyId === '' || item.common.name === ''){
                tag=true
            }
        })
        return tag
    }
    render() {
        return (
            <div className='top'>
                <span style={{float:'left',paddingLeft:'20px',fontWeight:'bold'}}>{window.sessionStorage.getItem('formManageName')}</span>
                <Button onClick={this.handleSave} type="primary"  icon="save" >保存</Button>
                <Button style={{marginLeft:10}} onClick={this.handlePreview} type="primary"  icon="eye" >预览</Button>
                <Button style={{marginLeft:10}} onClick={this.handleReset} type="primary"  icon="reload" >清空</Button>
            </div>
        )
    }
}

export default withRouter(MiddleTop)
