import React, { Component } from 'react'
import { Drawer, Tabs, Button,Modal } from 'antd'
import CommonProp from '../common'
import DataProp from '../data'
const { TabPane } = Tabs;
export default class MyDrawer extends Component {
    handleCancel = () => {
        this.props.handleCancel()
    }
    handleDeleteChild=()=>{
        Modal.confirm({
            title:'删除该列',
            content:'确定删除该列吗？',
            okText:'确定',
            cancelText:'取消',
            onOk:()=>{
                this.props.handleDeleteChild(this.props.activeItem.id)
            }
        })
    }
    render() {
        return (
            <Drawer
                visible={this.props.visible}
                width={300}
                // closable={false}
                bodyStyle={{padding:0}}
                onClose={this.handleCancel}
                closable={false}
            >   {
                    this.props.activeItem ? <Tabs defaultActiveKey="1">
                        <TabPane
                            tab={
                                <span>
                                    常规
                                </span>
                            }
                            key="1"
                        >
                            <CommonProp
                                activeItem={this.props.activeItem}
                                handleUpdateProp={this.props.handleUpdateProp}
                            />
                        </TabPane>
                        
                        <TabPane
                            tab={
                                <span>
                                    列数据
                                </span>
                            }
                            key="3"
                        >
                            <DataProp
                                list={this.props.parent.children}
                                activeItem={this.props.activeItem}
                                handleUpdateProp={this.props.handleUpdateProp}
                                parent={this.props.parent}
                            />
                        </TabPane>
                    </Tabs> : null
                }

                <div style={{ textAlign: 'center', position: 'absolute', left: 0, right: 0, bottom: 20 }}>
                    <Button onClick={this.props.handleSaveTable} type='primary' style={{ marginRight: 15 }}>保存</Button>
                    <Button onClick={this.handleDeleteChild} type='danger' style={{ marginRight: 15 }}>删除</Button>
                    <Button onClick={this.handleCancel}>取消</Button>
                </div>
            </Drawer>

        )
    }
}
