import React, { Component } from 'react'
import { WidthProvider, Responsive } from "react-grid-layout";
import { DropTarget } from 'react-dnd';
import { Icon,Empty,Modal} from 'antd'
import BaseText from './component/BaseText'
import BaseInput from './component/BaseInput'
import BaseTable from './component/BaseTable'
import BaseSelect from './component/BaseSelect'
import BaseRadio from './component/BaseRadio'
import BaseCheckbox from './component/BaseCheckbox'
import BaseDate from './component/BaseDate'
import BaseUpload from './component/BaseUpload'
import './index.less'
const ResponsiveReactGridLayout = WidthProvider(Responsive);
const rowTarget = {
    // drop返回的对象可以在drap里面拿到
    // component对应当前绑定的组件的this,更新状态使用setState
    drop(props, monitor, component) {        
        let dragData = monitor.getItem()
        if (dragData.type) {
            component.props.handleAddComponent(dragData.type)
        }
    },
};
class MiddleBottom extends Component {
    onLayoutChange=(currentLayout,allLayout)=>{        
        this.props.changeLayout(currentLayout)
    }
    handleDelete=(item)=>{
        Modal.confirm({
            title:'删除',
            content:'确定此组件删除吗？',
            okText:'确定',
            cancelText:'取消',
            onOk:()=>{
                this.props.handleDeleteComponent(item.id)
            }
        })
    }
    // 根据type分发不同的组件用于展示
    handleComponent=(item)=>{
        switch(item.type){
            case 'text':
                return <BaseText item={item}/>
            case 'input':
                return <BaseInput item={item}/>
            case 'table':
                return <BaseTable item={item}/>
            case 'dropDown':
                return <BaseSelect item={item}/>
            case 'radio':
                return <BaseRadio item={item}/>
            case 'checkbox':
                return <BaseCheckbox item={item}/>
            case 'date':
                return <BaseDate item={item}/>
            case 'upload':
                return <BaseUpload item={item}/>
            default:
                return null
        }
    }
    render() {
        let config = {
            className: "layout",
            cols: { lg: 24, md: 24, sm: 24, xs: 24, xxs: 24 },
            rowHeight: 50,
            // verticalCompact:false //是否自动压缩
        }
        const { connectDropTarget } = this.props
        let list=this.props.list.map(item=>{
            if(item){
                item.layout.minH=1           

            }
            return item
        })
        // console.log(this.props.list)
        return connectDropTarget(
            <div className='bottom sp-bottom'>
                <div className='container'>
                    {
                        list.length>0?<ResponsiveReactGridLayout
                        {
                        ...config
                        }
                        onLayoutChange={this.onLayoutChange}
                    >
                        {
                            this.props.list.map((item, index) => {
                                return (
                                    <div onClick={()=>{this.props.handleSelectComponent(item)}} key={item.id} className={`layout-item ${(this.props.activeItem && this.props.activeItem.id === item.id) ? 'active':''}`} data-grid={item.layout}>
                                        <div className='actions'>
                                            <Icon onClick={()=>{this.handleDelete(item)}} type="delete" style={{ color: '#fff', cursor: 'pointer' }} />
                                        </div>
                                        
                                        <div style={{overflowX:'auto',overflowY:'hidden',width:'100%'}}>
                                            {
                                                this.handleComponent(item)
                                            }
                                        </div>
                                    </div>
                                )
                            })
                        }

                    </ResponsiveReactGridLayout>:<Empty image={Empty.PRESENTED_IMAGE_SIMPLE} />
                    }
                    
                </div>
            </div>
        )
    }
}
