MiddleBottom = DropTarget(
    'desinger',
    rowTarget,
    (connect, monitor) => ({
        connectDropTarget: connect.dropTarget(),
        isOver: monitor.isOver(),
    }),
)(MiddleBottom)

export default MiddleBottom