import React, { Component } from 'react'
import {Modal,Input,Row,Col,Tag,Button,message} from 'antd'
import {postAction} from '@/axios';
const {TextArea}=Input
export default class MyModal extends Component {
    state={
        tag:true,
        value:this.props.BindDataSetConditions,
        colors:['magenta','red','volcano','orange','gold','lime','cyan','geekblue']
    }    
    handleChange=(e)=>{
        this.setState({
            value:e.target.value
        })
    }
    handleCheck=()=>{
        if(this.state.value === ''){
            message.warning('数据集条件不能为空')
            return
        }
        let dataSetId=this.props.activeItem.data.DataMember
        let param={
            dataSourceId:this.props.currentDataSource,
            dataSetId:dataSetId,
            condition:this.state.value
        }
        postAction('/ybt/form/widget/datasetcondition/validate',
            param
        ,1,1).then(res => {
            if(res.success){
                message.success('校验通过')
                this.setState({
                    tag:false
                })
            }else{
                message.error(res.obj)
            }
        })
        
    }
    handleSave=()=>{
        this.props.handleSaveShuju(this.state.value)
    }
    render() {
        return (
            <Modal
                visible={this.props.visible}
                title='设置数据集条件'                
                footer={null}
                onCancel={this.props.handleClose}
                width={800}
            >
                <Row>
                    <Col span={6} style={{height:400,overflow:'auto'}}>
                        {
                            this.props.fields.map((item,index)=>
                                <Tag key={index} style={{marginBottom:5}} color={this.state.colors[Math.round(Math.random()*7)]}>{item.fieldName}</Tag>
                            )
                        }                        
                    </Col>
                    <Col span={18} style={{height:400,overflow:'auto'}}>
                        <TextArea value={this.state.value} onChange={this.handleChange} rows={16}/>
                    </Col>
                </Row>
                <div style={{textAlign:'center',padding:'30px 0 0 0'}}>
                    <Button onClick={this.handleCheck} type='primary' style={{marginRight:15}}>
                        检验
                    </Button>
                    <Button onClick={this.handleSave} disabled={this.state.tag} type='primary' style={{marginRight:15}}>
                        确定
                    </Button>
                    <Button onClick={this.props.handleClose}>
                        取消
                    </Button>
                </div>
            </Modal>
        )
    }
}
