import React, { Component } from 'react'
import { Table } from 'antd'
import {generateStyle} from '../util'
export default class BaseTable extends Component {
    fixedCol = (index) => {
        if(this.props.item.common.SerialNumber){
            return index+2 <= this.props.item.common.fixCol
        }else{
            return index+1 <= this.props.item.common.fixCol
        }
    }
    render() {
        let tableWidth=0;
        let columns=this.props.item.children.map((item,index)=>{
            let width=150
            if(item.common.width){
                width=item.common.width
            }
            tableWidth +=Number(width)
            return {
                title:item.common.title,
                dataIndex:index,
                width,
                fixed:this.fixedCol(index)?'left':false
            }
        })
        if(this.props.item.common.SerialNumber){
            columns.unshift({
                title:'序号',
                dataIndex:'key',
                width:80,
                fixed:this.props.item.common.fixCol>0?'left':false
            })
        }
        let style=generateStyle(this.props.item.style) || {}
        return (
            <Table
                columns={columns}
                // style={{...style,width:`${tableWidth}px`,minWidth:926}}
                style={{...style,minWidth:926}}
                title={()=>{
                    return <div>{this.props.item.common.Caption}</div>
                }}
            >

            </Table>
        )
    }
}
