import React, { Component } from 'react'
import Left from './Left'
import Middle from './Middle'
import Right from './Right'
import './index.less'
// 导入拖拽的包
import {Button} from 'antd'
import { DragDropContext} from 'react-dnd';
import HTML5Backend from 'react-dnd-html5-backend';
import generateBaseData from './generateBaseData'
import {parseUrlSearch} from '../util'
import {withRouter} from 'react-router-dom'
import {getAction } from '@/axios';
class Designer extends Component {
    state={
        list:[],
        activeItem:null,
        param:null
    }
    componentDidMount() {
        let param=parseUrlSearch()
        if(param.id){
            this.setState({
                param
            },()=>{
                this.getData()
            })
        }
    }
    getData=()=>{
        let {param}=this.state
        getAction('/ybt/form/info',{
            formId:param.id
        }).then(res=>{
            if(res.success){
                // 拿到数据的时候对字段进行拆分
                let list=JSON.parse(res.obj.columnProperties) || [] 
                this.setState({
                    list:list
                })
            }
        });
    }
    // 拖入组件
    handleAddComponent=type=>{
        let addItem=generateBaseData(this.state.list,type)
        let newList=[...this.state.list,addItem]
        this.setState({
            list:newList,
            activeItem:addItem
        })
    }
    // 待优化的点，删除之后可以从删除的位置新增组件
    handleDeleteComponent=id=>{
        let newList=this.state.list.filter(item=>item.id !== id)
        this.setState({
            list:newList,
            activeItem:null
        })
    }
    handleSelectComponent=item=>{
        this.setState({
            activeItem:item
        })
    }
    // 改变整体布局,根据layoutArr更新全部组件的layout
    changeLayout=layoutArr=>{
        let tmpList=JSON.parse(JSON.stringify(this.state.list))
        tmpList=tmpList.map(item=>{
            let newLayout=this.searchLayout(item.id,layoutArr)
            item.layout={...item.layout,...newLayout}
            return item
        })
        this.setState({
            list:tmpList
        })
    }
    // 根据组件id去数组里面查找对应的组件layout
    searchLayout=(id,layoutArr)=>{
        return layoutArr.find(item=>item.i === id)
    }
    // 属性更新,同步到展示区域,需要同时更新list和activeItem
    handleUpdateProp=(type,id,fields)=>{        
        // console.log(fields)
        // 更新list里面对应的属性
        let tmpList=JSON.parse(JSON.stringify(this.state.list)),
        tmpItem=JSON.parse(JSON.stringify(this.state.activeItem))
        tmpList=tmpList.map(item=>{
            if(item.id === id){
                // 如果type是style，那么需要对颜色的值特殊处理
                if(type === 'style'){
                    fields.backgroundColor=fields.backgroundColor.color
                    fields.color=fields.color.color
                }
                item[type]={...item[type],...fields}                
            }
            return item
        })
        tmpItem[type]={...tmpItem[type],...fields} 
        this.setState({
            list:tmpList,
            activeItem:tmpItem
        })
    }
    // 清空全部组件
    handleReset=()=>{
        this.setState({
            list:[],
            activeItem:null
        })
    }
    // 删除表格的某一个列，需要的参数有表格的id和列id
    handleDeleteChild=(colId)=>{
        let id=this.state.activeItem.id,tmpList=JSON.parse(JSON.stringify(this.state.list)),
        tmpItem=JSON.parse(JSON.stringify(this.state.activeItem))

        // 先更新activeItem
        tmpItem.children=tmpItem.children.filter(item=>item.id !== colId)
        
        // 单独更新list，可优化
        tmpList.forEach(item=>{
            if(item.children && item.id === id){
                item.children=item.children.filter(item=>item.id !== colId)
            }
        })
        this.setState({
            list:tmpList,
            activeItem:tmpItem
        })
    }
    // 更新当前table组件的所有属性
    handleSaveTable=(tableItem)=>{        
        let id=this.state.activeItem.id,tmpList=JSON.parse(JSON.stringify(this.state.list)),
        tmpItem=JSON.parse(JSON.stringify(this.state.activeItem))
        // 判断是新增还是更新,tag是对应的下标
        let tag=tmpItem.children.findIndex(item=>item.id === tableItem.id)
        if(tag>=0){
            // 更新
            tmpItem.children.splice(tag,1,tableItem)
        }else{
            // 新增
            tmpItem.children=[...tmpItem.children,tableItem]
        }

        // 找出来当前组件是哪个位置的
        let current=tmpList.findIndex(item=>item.id === id)
        tmpList.splice(current,1,tmpItem)
        this.setState({
            list:tmpList,
            activeItem:tmpItem
        })
    }
    // 点击下一步保存数据
    handleNext=()=>{
        // if(this.state.list.length>0){
        //     // 需要对于table类型的数据做处理
        //     let list=this.state.list
        //     let params={
        //         "formId": this.state.param.id,
        //         "columnProperties": JSON.stringify(list),
        //     }
        //     postAction('/ybt/form/save',params).then(res=>{
        //         if(res.success){
        //             message.success('操作成功')  
        //             // 成功之后跳转下一步     
        //             this.props.history.push(`/formManagement/uploadTemplate?id=${this.state.param.id}`)    
        //         }else{
        //             message.error(res.obj)   
        //         }
        //     })
        // }else{
        //     message.warning('请先设计表单')
        //     return
        // }        
        // this.props.history.push(`/formManagement/uploadTemplate?id=${this.state.param.id}`)
    }
    render() {
        return (
            <div style={{padding:20,height:'100%',width:'100%',boxSizing:'border-box'}}>
                <div className='desinger-box'>
                    <Left/>
                    <Middle 
                        handleAddComponent={this.handleAddComponent}
                        changeLayout={this.changeLayout}
                        handleDeleteComponent={this.handleDeleteComponent}
                        handleSelectComponent={this.handleSelectComponent}
                        handleReset={this.handleReset}
                        list={this.state.list}
                        activeItem={this.state.activeItem}
                        param={this.state.param}
                    />
                    <Right
                        activeItem={this.state.activeItem}
                        handleUpdateProp={this.handleUpdateProp}
                        handleDeleteChild={this.handleDeleteChild}
                        list={this.state.list}
                        handleSaveTable={this.handleSaveTable}
                    />
                </div>
                <div className='prev_next'>
                    <Button onClick={()=>{this.props.history.replace(`/formManagement/setWorkFlow?id=${this.state.param.id}`)}} type='primary' ghost style={{marginRight:8}}>上一步</Button>
                    <Button onClick={()=>{this.handleNext()}} type='primary' style={{marginRight:8}}>下一步</Button>
                    <Button onClick={()=>{this.props.history.replace('/formManagement')}} style={{marginRight:40}}>取消</Button>
                </div>
            </div>
            
        )
    }
}
Designer = withRouter(DragDropContext(HTML5Backend)(Designer));
export default Designer
