import React, { Component } from 'react'
import {Tree,Button} from 'antd'
import MyDrawer from './drawer'
const {TreeNode} =Tree
export default class TableChildren extends Component {
    state={
        selectItem:null,
        selectedKeys:[]
    }
    renderTreeNodes = (data) => {
        return data.map((item) => {
            if (item.children) {
                return (
                    <TreeNode title={<span>{item.common.title}</span>} key={item.id} dataRef={item}>
                        {this.renderTreeNodes(item.children)}
                    </TreeNode>
                );
            }
            return <TreeNode title={<span>{item.common.title}</span>} key={item.id} dataRef={item} />;
        });
    }
    handleAddCol=()=>{
        let item={
            type: 'tableChild',
            id: `tableChild${new Date().getTime()}`,
            common: {
                title: '',
                name:'',
                mark: '',
                onlyId:`tableChild${new Date().getTime()}`,
                tmp_options:[],
                width:150,
                rowType:'input',
                options:'',
            },
            data: {
                DataMember: '',
                DataValueField: '',
                FillingMode: 'Manual',
                AssociatedControlsID: '',
                BindDataSetConditions: '',
                BindDataField:'',//值字段
                AssociationFields:'',
                BindDataValue:'',
                // 枚举类型的字典配置
                EnumerationMember:'',
                EnumerationKey:'',
                EnumerationValue:'',
            },
            style: {
                backgroundColor: '#fff',
                fontFamily: '宋体',
                textDecoration: 'none',
                fontSize: 12,
                color: '#555',
                FontBold: false,
                FontItalic: false,
            }
        }
        this.setState({
            selectItem:item
        })
    }
    handleSelect=(keys)=>{
        let current=this.props.activeItem.children.find(item=>item.id === keys[0])
        this.setState({
            selectItem:current,
            selectedKeys:keys
        })
    }
    handleCancel=()=>{
        this.setState({
            selectItem:null,
            selectedKeys:[]
        })
    }
    handleDeleteChild=(id)=>{
        this.setState({
            selectItem:null,
            selectedKeys:[]
        },()=>{
            this.props.handleDeleteChild(id)
        })
    }

    handleUpdateProp=(type,id,fields)=>{
        // 更新的时候把当前列的信息更新
        let tmpItem=JSON.parse(JSON.stringify(this.state.selectItem))
        if(type === 'style'){
            fields.backgroundColor=fields.backgroundColor.color
            fields.color=fields.color.color
        }
        tmpItem[type]={...tmpItem[type],...fields}
        this.setState({
            selectItem:tmpItem
        })       
    }
    // 新增或者更新表格列
    handleSaveTable=()=>{
        let item=this.state.selectItem
        this.setState({
            selectItem:null,
            selectedKeys:[]
        },()=>{
            this.props.handleSaveTable(item)
        })
    }
    render() {
        let treeData=this.props.activeItem.children || []
        let visible=this.state.selectItem?true:false;
        return (
            <div style={{ padding: '0 20px' }}>
                <div>
                    <Button onClick={this.handleAddCol} type='primary' icon='add'>添加列</Button>
                </div>
                <Tree
                    showLine={true}
                    defaultExpandAll
                    onSelect={this.handleSelect}          
                    selectedKeys={this.state.selectedKeys}          
                >
                    {
                        this.renderTreeNodes(treeData)
                    }
                </Tree>
                <MyDrawer 
                visible={visible} 
                activeItem={this.state.selectItem}
                handleCancel={this.handleCancel}
                handleDeleteChild={this.handleDeleteChild}
                handleUpdateProp={this.handleUpdateProp}
                handleSaveTable={this.handleSaveTable}
                parent={this.props.activeItem}
                />
            </div>
        )
    }
}
