// 定义基础数据结构
export default (arr, type) => {
    // 取得已有的列表中的layout的y的最大值
    let y = 0
    if (arr && arr.length > 0) {
        let yArr = arr.map(item => item.layout.y)
        y = Math.max.apply(Math, yArr)
    }
    let layout = {
        x: 0,
        y: y + 1,
        w: 24,
        h: 1,
        isDraggable: undefined,
        isResizable: undefined,
        maxH: undefined,
        maxW: undefined,
        minH: 1,
        minW: undefined,
        moved: false,
        static: false,
    }
    switch (type) {
        case 'text':
            return {
                type: 'text',
                id: `comp${new Date().getTime()}`,
                layout: layout,
                common: {
                    desc: '我是一段描述性的段落文字，你可以随意修改',
                    mark: '',
                    center:true
                },
                actualValue:"",
                style: {
                    backgroundColor: '#fff',
                    fontFamily: '宋体',
                    textDecoration: 'none',
                    fontSize: 12,
                    color: '#333',
                    FontBold: false,
                    FontItalic: false,
                }
            }
        case 'dropDown':
            return {
                type: 'dropDown',
                id: `comp${new Date().getTime()}`,
                layout: layout,
                actualValue:"",
                common: {
                    label: '下拉框',
                    mark: '',
                    options:'',
                    onlyId:`comp${new Date().getTime()}`,
                    name:''
                },
                data: {
                    DataMember: '',
                    DataValueField: '',
                    AssociatedControlsID: '',
                    BindDataSetConditions: '',
                    FillingMode: '',
                    // 枚举类型的字典配置
                    EnumerationMember:'',
                    EnumerationKey:'',
                    EnumerationValue:'',

                },
                style: {
                    backgroundColor: '#fff',
                    fontFamily: '宋体',
                    textDecoration: 'none',
                    fontSize: 12,
                    color: '#555',
                    FontBold: false,
                    FontItalic: false,
                }
            }
        case 'radio':
            return {
                type: 'radio',
                id: `comp${new Date().getTime()}`,
                layout: layout,
                actualValue:"",
                common: {
                    label: '单选框',
                    mark: '',
                    options:'',
                    onlyId:`comp${new Date().getTime()}`,
                    name:''
                },
                data:null,
                style: {
                    backgroundColor: '#fff',
                    fontFamily: '宋体',
                    textDecoration: 'none',
                    fontSize: 12,
                    color: '#555',
                    FontBold: false,
                    FontItalic: false,
                }
            }
        case 'checkbox':
            return {
                type: 'checkbox',
                id: `comp${new Date().getTime()}`,
                layout: layout,
                actualValue:"",
                common: {
                    label: '复选框',
                    mark: '',
                    options:'',
                    onlyId:`comp${new Date().getTime()}`,
                    name:''
                },
                data:null,
                style: {
                    backgroundColor: '#fff',
                    fontFamily: '宋体',
                    textDecoration: 'none',
                    fontSize: 12,
                    color: '#555',
                    FontBold: false,
                    FontItalic: false,
                }
            }
        case 'date':
            return {
                type: 'date',
                id: `comp${new Date().getTime()}`,
                layout: layout,
                actualValue:"",
                common: {
                    label: '日期',
                    mark: '',
                    ReadOnly: false,
                    MustFill: false,
                    onlyId:`comp${new Date().getTime()}`,
                    name:''
                },
                data: {
                    DataMember: '',
                    DataValueField: '',
                    AssociatedControlsID: '',
                    BindDataSetConditions: '',
                    FillingMode: 'Manual',
                },
                style: {
                    backgroundColor: '#fff',
                    fontFamily: '宋体',
                    textDecoration: 'none',
                    fontSize: 12,
                    color: '#555',
                    FontBold: false,
                    FontItalic: false,
                }
            }
        case 'upload':
            return {
                type: 'upload',
                id: `comp${new Date().getTime()}`,
                layout: layout,
                common: {
                    label: '文件上载',
                    mark: '',
                    AllowMultiple: true,
                    MustFill: false,
                },
                data: {
                },
                style: {
                    backgroundColor: '#fff',
                    fontFamily: '宋体',
                    textDecoration: 'none',
                    fontSize: 12,
                    color: '#555',
                    FontBold: false,
                    FontItalic: false,
                }
            }
        case 'input':
            return {
                type: 'input',
                id: `comp${new Date().getTime()}`,
                layout: layout,
                actualValue:"",
                common: {
                    label: '输入框',
                    mark: '',
                    ReadOnly: false,
                    MustFill: false,
                    TextMode: 'SingleLine',
                    Rows: 3,
                    regex: '',
                    zdyRegex:'',
                    onlyId:`comp${new Date().getTime()}`,
                    name:''
                },
                data: {
                    DataMember: '',
                    DataValueField: '',
                    BindDataField:'',//值字段
                    AssociatedControlsID: '',
                    BindDataSetConditions: '',
                    FillingMode: 'Manual',
                    AssociationFields:'',
                    BindDataValue:'',
                    formula:''
                },
                style: {
                    backgroundColor: '#fff',
                    fontFamily: '宋体',
                    textDecoration: 'none',
                    fontSize: 12,
                    color: '#555',
                    FontBold: false,
                    FontItalic: false,
                }
            }
        case 'table':
            // 表格的栅格布局默认给多一点
            layout.w = 24
            layout.h = 5
            return {
                type: 'table',
                id: `comp${new Date().getTime()}`,
                layout: layout,
                actualValue:[],
                common: {
                    Caption: '表格标题',
                    // CaptionAlign: '',
                    mark: '',
                    onlyId:`comp${new Date().getTime()}`,
                    name:'',
                    SerialNumber: false,
                    ReadOnly:false,
                    fixCol:0,//固定前几列
                },
                data: {
                    DataMember: '',
                    FillingMode: 'AutomaticFilling',
                    AssociatedControlsID: '',
                    BindDataSetConditions: '',
                },
                style: null,
                children: [
                    
                ]
            }
        default:
            return null
    }
}