import React, { Component } from 'react'
import {Form,Input} from 'antd'
import {generateStyle} from '../util'
const FormItem=Form.Item
const { TextArea } = Input
class BaseInput extends Component {
    render() {
        const { getFieldDecorator } = this.props.form;
        let item=this.props.item
        // const formItemLayout = {
        //     labelCol: {
        //       span:4
        //     },
        //     wrapperCol: {
        //       span:20
        //     },
        // };
        let style=generateStyle(this.props.item.style) || {}
        return (
            <Form 
                // {...formItemLayout}
            >
                {
                    item.common.TextMode === 'SingleLine'?<FormItem labelCol={{span:4}} wrapperCol={{span:20}} label={item.common.label}>
                    {
                        getFieldDecorator('input',{

                        })(<Input  style={style} readOnly/>)
                    }
                </FormItem>:<FormItem  labelCol={{span:4}} wrapperCol={{span:24}} label={item.common.label}>
                    {
                        getFieldDecorator('input',{

                        })(<TextArea  style={style} rows={item.common.Rows} readOnly/>)
                    }
                </FormItem>
                }
                
            </Form>
        )
    }
}
BaseInput=Form.create()(BaseInput)
export default BaseInput
