import React, { Component } from 'react'
import { Tabs, Empty } from 'antd'
import CommonProp from './common'
import DataProp from './data'
import Styleprop from './style'
import TableChildren from './tableChildren'
const { TabPane } = Tabs;
export default class Right extends Component {
    render() {
        let rightChildren = <Empty image={Empty.PRESENTED_IMAGE_SIMPLE} />
        if (this.props.activeItem) {
            rightChildren = <Tabs defaultActiveKey="1" >
                <TabPane
                    tab={
                        <span>
                            常规
                        </span>
                    }
                    key="1"
                >
                    <CommonProp
                        activeItem={this.props.activeItem}
                        handleUpdateProp={this.props.handleUpdateProp}
                    />
                </TabPane>

                {
                    this.props.activeItem.style ? <TabPane
                        tab={
                            <span>
                                样式
                            </span>
                        }
                        key="2"
                    >
                        <Styleprop
                            activeItem={this.props.activeItem}
                            handleUpdateProp={this.props.handleUpdateProp}
                        />
                    </TabPane> : null
                }
                {
                    this.props.activeItem.data ?
                        <TabPane
                            tab={
                                <span>
                                    {this.props.activeItem.type === 'table'?'表数据':'数据'}
                                </span>
                            }
                            key="3"
                        >
                            <DataProp
                                activeItem={this.props.activeItem}
                                handleUpdateProp={this.props.handleUpdateProp}
                                list={this.props.list}
                            />
                        </TabPane> : null
                }

                {
                    this.props.activeItem.children ? <TabPane
                        tab={
                            <span>
                                列
                            </span>
                        }
                        key="4"
                    >
                        <TableChildren
                            activeItem={this.props.activeItem}
                            handleUpdateProp={this.props.handleUpdateProp}
                            handleDeleteChild={this.props.handleDeleteChild}
                            handleSaveTable={this.props.handleSaveTable}
                        />
                    </TabPane> : null
                }

            </Tabs>
        }
        return (
            <div className='desinger-right'>
                {rightChildren}
            </div>
        )
    }
}
