import React, { Component } from 'react'
import { Form, Select, Input, message } from 'antd'
import MyModal from '../../common/modal'
import { getAction } from '@/axios';
const FormItem = Form.Item, Option = Select.Option
class PropComponent extends Component {
    state = {
        shujuji: [],
        fields: [],
        enumerationShujuji: [],
        enumerationFields: [],
        currentEnumerationShujuji: '',
        visible: false,
        currentShujuji: '',
        currentDataSource: '',
        fillMode: [
            {
                name: '手动输入',
                value: 'Manual'
            },
            {
                name: '联想',
                value: 'Association'
            },
            {
                name: '关联填充',
                value: 'AssociationFilling'
            },
            {
                name: '自动填充',
                value: 'AutomaticFilling'
            }
        ]
    }
    componentDidMount() {
        this.getDataMembers()
        // 判断如果已选数据集需要直接请求字段列表
        if (this.props.activeItem.data.hasOwnProperty('DataMember') && this.props.activeItem.data.DataMember !== '') {
            this.getFields(this.props.activeItem.data.DataMember)
        }
        if (this.props.activeItem.data.hasOwnProperty('EnumerationMember') && this.props.activeItem.data.EnumerationMember !== '') {
            this.getEnumerationFields(this.props.activeItem.data.EnumerationMember)
        }
        // 如果是表格的子组件，需要先直接拿已选数据集的下面的字段
        if (this.props.activeItem.type === 'tableChild' && this.props.activeItem.data.FillingMode === 'AutomaticFilling') {
            // console.log(this.props.parent.data.DataMember)
            this.getFields(this.props.parent.data.DataMember)
        }

    }
    getDataMembers = () => {
        getAction('/ybt/data/dataset/folder/list?pageNum=0&pageSize=0', {
        }).then(res => {
            if (res.success) {
                this.setState({
                    shujuji: res.obj.rows,
                    enumerationShujuji: res.obj.rows
                })
            }
        });
    }
    componentDidUpdate(preProps){
        if (preProps.activeItem.id !== this.props.activeItem.id) {
            // 重置所有表单项 
            this.props.form.resetFields()
            this.setState({
                currentShujuji: '',
                currentDataSource: '',
                currentEnumerationShujuji: ''
            })
        }
    }

    getDataSoureId = (id) => {
        let item = this.state.shujuji.find(item => item.dataSetId === id)
        if (item) {
            return item.dataSourceId
        } else {
            return null
        }
    }
    // 处理枚举数据集变化
    handleEnumerationShujujiChange = (value) => {
        this.getEnumerationFields(value)
    }
    handleShujujiChange = (value) => {
        let id = this.getDataSoureId(value)
        if (id) {
            this.setState({
                currentDataSource: id
            })
        }
        this.getFields(value)
    }
    getEnumerationFields = id => {
        getAction('/ybt/data/dataset/field/list', {
            dataSetId: id
        }).then(res => {
            if (res.success) {
                this.setState({
                    enumerationFields: res.obj
                })
            }
        });
    }
    getFields = id => {
        getAction('/ybt/data/dataset/field/list', {
            dataSetId: id
        }).then(res => {
            if (res.success) {
                this.setState({
                    fields: res.obj
                })
            }
        });
    }
    handleClose = () => {
        this.setState({
            visible: false
        })
    }
    handleOpenModal = () => {
        if (this.state.fields.length <= 0) {
            message.warning('请先选择数据集')
            return
        }
        this.setState({
            visible: true
        })
    }
    handleSaveShuju = (value) => {
        // 更新到form组件对应的键
        this.setState({
            visible: false
        }, () => {
            this.props.form.setFieldsValue({ 'BindDataSetConditions': value }, () => { })
        })
    }
    handleFocus = () => {
        if (this.state.fields.length <= 0) {
            message.warning('请先选择数据集')
            return
        }
    }
    handleEnumerationFocus = () => {
        if (this.state.enumerationFields.length <= 0) {
            message.warning('请先选择枚举数据集')
            return
        }
    }
    render() {
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol: {
                span: 8
            },
            wrapperCol: {
                span: 16
            },
        };
        // 根据list里面的唯一标识和组件名称生成控件下拉框的选项
        let selectList = this.props.list.map((item, index) => {
            if (item.common.onlyId) {
                // 排除自身选项，排除单选框、复选框和下拉框
                if (item.type === 'radio' || item.type === 'checkbox') {
                    return null
                } else {
                    if (this.props.activeItem.id !== item.id) {
                        return {
                            name: item.common.name,
                            value: item.common.onlyId
                        }
                    } else {
                        return null
                    }
                }

            } else {
                return null
            }
        })
        // 过滤多余的null防止报错
        selectList = selectList.filter(item => item)
        // 根据组件类型过滤模式选项
        let fillMode = this.state.fillMode
        if (this.props.activeItem.type === 'date' || this.props.activeItem.type === 'table' || this.props.activeItem.type === 'dropDown' || this.props.activeItem.common.rowType === 'date' || this.props.activeItem.common.rowType === 'select') {
            fillMode = fillMode.filter(item => item.value !== 'Association')
        }
        let shujujiTiaojian = null
        // 如果是表格列组件并且填充模式是自动填充，就不显示数据集条件
        if (this.props.activeItem.type === 'tableChild') {
            if (this.props.activeItem.data.hasOwnProperty('BindDataSetConditions') && (this.props.activeItem.data.FillingMode === 'Association' || this.props.activeItem.data.FillingMode === 'AssociationFilling')) {
                shujujiTiaojian = <FormItem label='数据集条件'>
                    {
                        getFieldDecorator('BindDataSetConditions', {
                            initialValue: this.props.activeItem.data.BindDataSetConditions,
                        })(<Input onClick={() => { this.handleOpenModal() }} />)
                    } </FormItem>
            } else {
                shujujiTiaojian = null
            }
        } else {
            if (this.props.activeItem.data.hasOwnProperty('BindDataSetConditions')) {
                shujujiTiaojian = <FormItem label='数据集条件'>
                    {
                        getFieldDecorator('BindDataSetConditions', {
                            initialValue: this.props.activeItem.data.BindDataSetConditions,
                        })(<Input onClick={() => { this.handleOpenModal() }} />)
                    }
                </FormItem>
            }
        }


        return (
            <React.Fragment>
                {
                    this.state.visible ? <MyModal
                        handleClose={this.handleClose}
                        visible={this.state.visible}
                        BindDataSetConditions={this.props.activeItem.data.BindDataSetConditions}
                        handleSaveShuju={this.handleSaveShuju}
                        fields={this.state.fields}
                        activeItem={this.props.activeItem}
                        currentDataSource={this.state.currentDataSource}
                    /> : null
                }

                <Form {...formItemLayout} style={{ padding: '0 10px' }}>
                    {
                        this.props.activeItem.data.hasOwnProperty('FillingMode') ? <FormItem label='填充模式'>
                            {
                                getFieldDecorator('FillingMode', {
                                    initialValue: this.props.activeItem.data.FillingMode,
                                })(<Select>
                                    {
                                        fillMode.map((item, index) => <Option key={index} value={item.value}>{item.name}</Option>)
                                    }
                                </Select>)
                            }
                        </FormItem> : null
                    }
                    {
                        this.props.activeItem.data.hasOwnProperty('FillingMode') && this.props.activeItem.data.FillingMode === 'Manual' && this.props.activeItem.type === 'input' ? <FormItem label='计算公式'>
                            {
                                getFieldDecorator('formula', {
                                    initialValue: this.props.activeItem.data.formula,
                                })(<Input.TextArea />)
                            }
                        </FormItem> : null
                    }
                    {
                        this.props.activeItem.data.hasOwnProperty('FillingMode') && this.props.activeItem.data.FillingMode !== 'Manual' && this.props.activeItem.data.FillingMode !== '' ?
                            <React.Fragment>
                                {
                                    this.props.activeItem.data.hasOwnProperty('DataMember') ?
                                        <FormItem label='数据集'>
                                            {
                                                getFieldDecorator('DataMember', {
                                                    initialValue: this.props.activeItem.data.DataMember,
                                                })(
                                                    // 带模糊搜索的下拉框
                                                    <Select
                                                        filterOption={(input, option) => option.props.children.indexOf(input) >= 0}
                                                        showSearch
                                                        optionFilterProp="children"
                                                        onChange={this.handleShujujiChange}
                                                    >
                                                        {
                                                            this.state.shujuji.map((item, index) => <Option key={index} value={item.dataSetId}>{item.dataSetName}</Option>)
                                                        }
                                                    </Select>)
                                            }
                                        </FormItem> : null
                                }
                                {
                                    this.props.activeItem.data.hasOwnProperty('DataValueField') ? <FormItem label='字段'>
                                        {
                                            getFieldDecorator('DataValueField', {
                                                initialValue: this.props.activeItem.data.DataValueField,
                                            })(<Select
                                                filterOption={(input, option) => option.props.children.indexOf(input) >= 0}
                                                showSearch
                                                optionFilterProp="children"
                                                onFocus={this.handleFocus}
                                            >
                                                {
                                                    this.state.fields.map((item, index) => <Option key={index} value={item.fieldName}>{item.fieldName}</Option>)
                                                }
                                            </Select>)
                                        }
                                    </FormItem> : null
                                }
                                {
                                    this.props.activeItem.data.hasOwnProperty('BindDataField') && this.props.activeItem.data.FillingMode === 'Association' ? <FormItem label='值字段'>
                                        {
                                            getFieldDecorator('BindDataField', {
                                                initialValue: this.props.activeItem.data.BindDataField,
                                            })(<Select
                                                filterOption={(input, option) => option.props.children.indexOf(input) >= 0}
                                                showSearch
                                                optionFilterProp="children"
                                                onFocus={this.handleFocus}
                                            >
                                                {
                                                    this.state.fields.map((item, index) => <Option key={item.dataSetFieldId} value={item.fieldName}>{item.fieldName}</Option>)
                                                }
                                            </Select>)
                                        }
                                    </FormItem> : null
                                }
                                {
                                    this.props.activeItem.data.hasOwnProperty('AssociationFields') && this.props.activeItem.data.FillingMode === 'Association' ? <FormItem label='联想字段'>
                                        {
                                            getFieldDecorator('AssociationFields', {
                                                initialValue: this.props.activeItem.data.AssociationFields !== '' ? this.props.activeItem.data.AssociationFields : [],
                                            })(<Select
                                                filterOption={(input, option) => option.props.children.indexOf(input) >= 0}
                                                showSearch
                                                optionFilterProp="children"
                                                mode="multiple"
                                                onFocus={this.handleFocus}
                                            >
                                                {
                                                    this.state.fields.map((item, index) => <Option key={item.dataSetFieldId} value={item.fieldName}>{item.fieldName}</Option>)
                                                }
                                            </Select>)
                                        }
                                    </FormItem> : null
                                }
                                {
                                    this.props.activeItem.data.hasOwnProperty('AssociatedControlsID') && this.props.activeItem.data.FillingMode === 'AssociationFilling' ? <FormItem label='关联控件'>
                                        {
                                            getFieldDecorator('AssociatedControlsID', {
                                                initialValue: this.props.activeItem.data.AssociatedControlsID,
                                            })(<Select>
                                                {
                                                    selectList.map((item, index) => <Option key={index} value={item.value}>{item.name}</Option>)
                                                }
                                            </Select>)
                                        }
                                    </FormItem> : null
                                }
                                {
                                    shujujiTiaojian
                                }
                            </React.Fragment> 
                            : null
                    }
                    {/* 当前版本只判断下拉框才可配置枚举，包括单独的和表格内嵌的 */}

                    {
                                    (this.props.activeItem.type === 'dropDown' || this.props.activeItem.common.rowType === 'select') &&
                                    <React.Fragment>
                                        {
                                            this.props.activeItem.data.hasOwnProperty('EnumerationMember') ?
                                                <FormItem label='枚举数据集'>
                                                    {
                                                        getFieldDecorator('EnumerationMember', {
                                                            initialValue: this.props.activeItem.data.EnumerationMember,
                                                        })(
                                                            // 带模糊搜索的下拉框
                                                            <Select
                                                                filterOption={(input, option) => option.props.children.indexOf(input) >= 0}
                                                                showSearch
                                                                optionFilterProp="children"
                                                                onChange={this.handleEnumerationShujujiChange}
                                                            >
                                                                {
                                                                    this.state.enumerationShujuji.map((item, index) => <Option key={index} value={item.dataSetId}>{item.dataSetName}</Option>)
                                                                }
                                                            </Select>)
                                                    }
                                                </FormItem> : null
                                        }
                                        {
                                            this.props.activeItem.data.hasOwnProperty('EnumerationKey') ? <FormItem label='枚举Key'>
                                                {
                                                    getFieldDecorator('EnumerationKey', {
                                                        initialValue: this.props.activeItem.data.EnumerationKey,
                                                    })(<Select
                                                        filterOption={(input, option) => option.props.children.indexOf(input) >= 0}
                                                        showSearch
                                                        optionFilterProp="children"
                                                        onFocus={this.handleEnumerationFocus}
                                                    >
                                                        {
                                                            this.state.enumerationFields.map((item, index) => <Option key={index} value={item.fieldName}>{item.fieldName}</Option>)
                                                        }
                                                    </Select>)
                                                }
                                            </FormItem> : null
                                        }
                                        {
                                            this.props.activeItem.data.hasOwnProperty('EnumerationValue') ? <FormItem label='枚举Value'>
                                                {
                                                    getFieldDecorator('EnumerationValue', {
                                                        initialValue: this.props.activeItem.data.EnumerationValue,
                                                    })(<Select
                                                        filterOption={(input, option) => option.props.children.indexOf(input) >= 0}
                                                        showSearch
                                                        optionFilterProp="children"
                                                        onFocus={this.handleEnumerationFocus}
                                                    >
                                                        {
                                                            this.state.enumerationFields.map((item, index) => <Option key={index} value={item.fieldName}>{item.fieldName}</Option>)
                                                        }
                                                    </Select>)
                                                }
                                            </FormItem> : null
                                        }
                                    </React.Fragment>
                                }


                </Form>
            </React.Fragment>

        )
    }
}
PropComponent = Form.create({
    onValuesChange: (props, changedFields, allFields) => {
        // 传入属性类型和组件id以及最新的属性去更新组件
        props.handleUpdateProp('data', props.activeItem.id, allFields)
    }
})(PropComponent)
export default PropComponent

