import React, { Component } from 'react'
import { Form, Switch, Select, Input, InputNumber } from 'antd'
import BaseRegex from '../../common/regex'
const FormItem = Form.Item, Option = Select.Option,{TextArea}=Input
class PropComponent extends Component {
    state = {
        selectMode: [
            {
                name: '单行输入模式',
                value: 'SingleLine'
            },
            {
                name: '多行输入模式',
                value: 'MultLine'
            }
        ],
        rowTypes:[
            {
                name:'单行输入框',
                value: 'input'
            },
            {
                name:'多行输入框',
                value: 'textarea'
            },
            {
                name:'日期',
                value: 'date'
            },
            {
                name:'下拉框',
                value: 'select'
            }
        ]
    }
    componentDidUpdate(preProps){
        if (preProps.activeItem.id !== this.props.activeItem.id) {
            // 重置所有表单项 
            this.props.form.resetFields()
        }
    }
   
    render() {
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol: {
                span: 8
            },
            wrapperCol: {
                span: 16
            },
        };
        return (
            <Form {...formItemLayout} style={{ padding: '0 10px' }}>
                {
                    this.props.activeItem.common.hasOwnProperty('onlyId') ? <FormItem label='唯一标识'>
                        {
                            getFieldDecorator('onlyId', {
                                initialValue: this.props.activeItem.common.onlyId === '' ?this.props.activeItem.id:this.props.activeItem.common.onlyId,
                                rules:[
                                    {
                                        pattern:/[a-zA-Z1-9]+/,
                                        message:'唯一标识格式有误'
                                    },
                                    {
                                        required:true,
                                        message:'唯一标识不能为空'
                                    }
                                ],
                                validateTrigger: ['onBlur'],
                            })(<Input />)
                        }
                    </FormItem> : null
                }
                {
                    this.props.activeItem.common.hasOwnProperty('name') ? <FormItem label='控件名称'>
                        {
                            getFieldDecorator('name', {
                                initialValue: this.props.activeItem.common.name,
                                rules:[
                                    {
                                        required:true,
                                        message:'控件名称不能为空'
                                    }
                                ],
                                validateTrigger: ['onBlur'],
                            })(<Input />)
                        }
                    </FormItem> : null
                }
                {
                    this.props.activeItem.type === 'tableChild' ||this.props.activeItem.common.hasOwnProperty('rowType') ? <FormItem label='列类型'>
                        {
                            getFieldDecorator('rowType', {
                                initialValue: this.props.activeItem.common.rowType||'input'
                            })(<Select>
                                {
                                    this.state.rowTypes.map((item, index) => <Option key={index} value={item.value}>{item.name}</Option>)
                                }
                            </Select>)
                        }
                    </FormItem> : null
                }
                {
                    this.props.activeItem.common.hasOwnProperty('title') ? <FormItem label='列名称'>
                        {
                            getFieldDecorator('title', {
                                initialValue: this.props.activeItem.common.title
                            })(<Input />)
                        }
                    </FormItem> : null
                }
                {
                    this.props.activeItem.common.hasOwnProperty('desc') ? <FormItem label='文本'>
                        {
                            getFieldDecorator('desc', {
                                initialValue: this.props.activeItem.common.desc
                            })(<TextArea rows={5} />)
                        }
                    </FormItem> : null
                }
                {
                    this.props.activeItem.common.hasOwnProperty('label') ? <FormItem label='Label'>
                        {
                            getFieldDecorator('label', {
                                initialValue: this.props.activeItem.common.label
                            })(<Input />)
                        }
                    </FormItem> : null
                }
                {
                    this.props.activeItem.common.hasOwnProperty('center') ? <FormItem label='文本居中'>
                        {
                            getFieldDecorator('center', {
                                initialValue: this.props.activeItem.common.center,
                                valuePropName: 'checked'
                            })(<Switch />)
                        }
                    </FormItem> : null
                }
                
                
                {
                    this.props.activeItem.common.hasOwnProperty('ReadOnly') ? <FormItem label='只读'>
                        {
                            getFieldDecorator('ReadOnly', {
                                initialValue: this.props.activeItem.common.ReadOnly,
                                valuePropName: 'checked'
                            })(<Switch />)
                        }
                    </FormItem> : null
                }
                {
                    this.props.activeItem.common.hasOwnProperty('MustFill') ? <FormItem label='必填'>
                        {
                            getFieldDecorator('MustFill', {
                                initialValue: this.props.activeItem.common.MustFill,
                                valuePropName: 'checked'
                            })(<Switch />)
                        }
                    </FormItem> : null
                }
                {
                    this.props.activeItem.common.hasOwnProperty('AllowMultiple') ? <FormItem label='多文件上载'>
                        {
                            getFieldDecorator('AllowMultiple', {
                                initialValue: this.props.activeItem.common.AllowMultiple,
                                valuePropName: 'checked'
                            })(<Switch />)
                        }
                    </FormItem> : null
                }
                {
                    this.props.activeItem.common.hasOwnProperty('TextMode') ? <FormItem label='文本框模式'>
                        {
                            getFieldDecorator('TextMode', {
                                initialValue: this.props.activeItem.common.TextMode
                            })(<Select onChange={this.handleChangeTextMode}>
                                {
                                    this.state.selectMode.map((item, index) => <Option key={index} value={item.value}>{item.name}</Option>)
                                }
                            </Select>)
                        }
                    </FormItem> : null
                }
                {
                    this.props.activeItem.common.hasOwnProperty('TextMode') && this.props.activeItem.common.TextMode === 'MultLine' ? <FormItem label='行数'>
                        {
                            getFieldDecorator('Rows', {
                                initialValue: this.props.activeItem.common.Rows
                            })(<InputNumber min={3} />)
                        }
                    </FormItem> : null
                }               
                {
                    this.props.activeItem.common.hasOwnProperty('ToolTip') ? <FormItem label='控件提示'>
                        {
                            getFieldDecorator('ToolTip', {
                                initialValue: this.props.activeItem.common.ToolTip
                            })(<Input />)
                        }
                    </FormItem> : null
                }
                {
                    this.props.activeItem.common.hasOwnProperty('Caption') ? <FormItem label='表格标题'>
                        {
                            getFieldDecorator('Caption', {
                                initialValue: this.props.activeItem.common.Caption
                            })(<Input />)
                        }
                    </FormItem> : null
                }
                {
                    this.props.activeItem.common.hasOwnProperty('Caption') ? <FormItem label='固定前N列'>
                        {
                            getFieldDecorator('fixCol', {
                                initialValue: this.props.activeItem.common.fixCol
                            })(<InputNumber />)
                        }
                    </FormItem> : null
                }
                {
                    this.props.activeItem.common.hasOwnProperty('CaptionAlign') ? <FormItem label='标题对齐方式'>
                        {
                            getFieldDecorator('CaptionAlign', {
                                initialValue: this.props.activeItem.common.CaptionAlign
                            })(<Select>
                                {
                                    this.state.selectMode.map((item, index) => <Option key={index} value={item.value}>{item.name}</Option>)
                                }
                            </Select>)
                        }
                    </FormItem> : null
                }
                {
                    this.props.activeItem.common.hasOwnProperty('regex') ? <FormItem label='校验规则'>
                        {
                            getFieldDecorator('regex', {
                                initialValue: this.props.activeItem.common.regex
                            })(<Select>
                                {
                                    BaseRegex.map((item, index) => <Option key={index} value={item.value}>{item.name}</Option>)
                                }
                            </Select>)
                        }
                    </FormItem> : null
                }
                {
                    this.props.activeItem.common.hasOwnProperty('regex') && this.props.activeItem.common.regex === 'zdy' ? <FormItem label='自定义规则'>
                        {
                            getFieldDecorator('zdyRegex', {
                                initialValue: this.props.activeItem.common.zdyRegex
                            })(<Input />)
                        }
                    </FormItem> : null
                }
                {
                    this.props.activeItem.common.hasOwnProperty('SerialNumber') ? <FormItem label='显示序号'>
                        {
                            getFieldDecorator('SerialNumber', {
                                initialValue: this.props.activeItem.common.SerialNumber,
                                valuePropName: 'checked'
                            })(<Switch />)
                        }
                    </FormItem> : null
                }
                {
                    (this.props.activeItem.common.hasOwnProperty('width')||this.props.activeItem.type==='tableChild') ? <FormItem extra='默认单位：px' label='列宽度'>
                        {
                            getFieldDecorator('width', {
                                initialValue: this.props.activeItem.common.width || 150,
                            })(<InputNumber />)
                        }
                    </FormItem> : null
                }
                {
                    (this.props.activeItem.type === 'dropDown' || this.props.activeItem.common.rowType === 'select' ||this.props.activeItem.type === 'checkbox'||this.props.activeItem.type === 'radio') &&
                    <React.Fragment>
                        {
                            this.props.activeItem.common.hasOwnProperty('options') ? <FormItem help='（配置项以逗号分隔）' label='选项配置'>
                                {
                                    getFieldDecorator('options', {
                                        initialValue: this.props.activeItem.common.options
                                    })(<TextArea />)
                                }
                            </FormItem> : null
                        }
                    </React.Fragment>
                }
            </Form>
        )
    }
}
PropComponent = Form.create({
    onValuesChange: (props, changedFields, allFields) => {
        // 传入属性类型和组件id以及最新的属性去更新组件
        props.handleUpdateProp('common', props.activeItem.id, allFields)
    }
})(PropComponent)
export default PropComponent

